"use client";

import { createContext, useState, useEffect } from "react";
import authService from "../services/authService";

// Create context
export const AuthContext = createContext();

// Context provider
export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [loading, setLoading] = useState(true);

  // Load user on app start
  useEffect(() => {
    const token = localStorage.getItem(
      process.env.NEXT_PUBLIC_JWT_STORAGE_KEY
    );
    
    if (token) {
      authService
        .getProfile()
        .then((res) => {
          setUser(res);
          setIsAuthenticated(true);
        })
        .catch(() => {
          // Try to refresh token
          authService
            .refreshToken()
            .then(() => {
              // Retry getting profile after refresh
              return authService.getProfile();
            })
            .then((res) => {
              setUser(res);
              setIsAuthenticated(true);
            })
            .catch(() => {
              // If refresh also fails, clear everything
              localStorage.removeItem(process.env.NEXT_PUBLIC_JWT_STORAGE_KEY);
              localStorage.removeItem(process.env.NEXT_PUBLIC_JWT_REFRESH_KEY || "refresh_token");
              setUser(null);
              setIsAuthenticated(false);
            });
        })
        .finally(() => setLoading(false));
    } else {
      setLoading(false);
    }
  }, []);

  // Login
  const login = async (email, password) => {
    try {
      const res = await authService.login(email, password);
      const { token, refresh } = res;
      
      // Store both tokens
      localStorage.setItem(process.env.NEXT_PUBLIC_JWT_STORAGE_KEY, token);
      localStorage.setItem(
        process.env.NEXT_PUBLIC_JWT_REFRESH_KEY || "refresh_token",
        refresh
      );

      // Fetch user info
      const userInfo = await authService.getProfile();
      setUser(userInfo);
      setIsAuthenticated(true);

      return { success: true };
    } catch (err) {
      console.error("Login error:", err);
      return { 
        success: false, 
        message: err.detail || err.message || "Login failed" 
      };
    }
  };

  // Signup
  const signup = async (userData) => {
    try {
      await authService.signup(userData);
      return { success: true };
    } catch (err) {
      console.error("Signup error:", err);
      
      // Django returns validation errors as objects
      let errorMessage = "Signup failed";
      
      if (typeof err === 'object') {
        // Extract first error message
        const firstError = Object.values(err)[0];
        if (Array.isArray(firstError)) {
          errorMessage = firstError[0];
        } else if (typeof firstError === 'string') {
          errorMessage = firstError;
        }
      } else if (err.message) {
        errorMessage = err.message;
      }
      
      return { success: false, message: errorMessage };
    }
  };

  // Logout
  const logout = () => {
    authService.logout();
    setUser(null);
    setIsAuthenticated(false);
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        isAuthenticated,
        login,
        logout,
        signup,
        loading,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};